import { mapActions } from "vuex";
import { mapState } from "vuex";

export default {
  name: 'navbar',
  components: {},
  props: [],
  data() {
    return {
      opciones: [
        { texto: 'Mi Perfil', link: '/MiPerfil' },
        { texto: 'Cerrar sesión', link: '/Login' }
      ]
    }
  },
  computed: {
    ...mapState("auth", ["usuario"])

  },
  methods: {
    ...mapActions("auth", ["salir"]),
    clickOpcion(i) {
      if (i == 0) {
        this.$router.push({ path: '/MiPerfil' })         
      } else if (i == 1) {
        this.salir()
      }
    }

  }
}


